"use client";
import useAuthStore from "@/store/MyAuthState";
import { useFetchData } from "@/utils/axiosData/getData";
import { Button, Table } from "antd";
import Image from "next/image";
import { useState } from "react";
import AddUsers from "./edits/AddUsers";

export type UserType = {
  id: number;
  name: string;
  email: string;
  password: string;
  image: string;
  role: string;
  createdAt: string;
};

type UserDatatype = {
  items: UserType[];
  message: string;
  page: number;
  total: number;
};

function UsersPage() {
  const MyAuthState = useAuthStore();
  const [AddOpen, setAddOpen] = useState(false);
  const [editOpen, setEditOpen] = useState(false);
  const [EditStudent, setEditStudent] = useState<UserType | null>(null);
  const { data: userData } = useFetchData<UserDatatype>(`/app/users/`);

  const showDrawer = () => setAddOpen(true);
  const onClose = () => setAddOpen(false);
  const user = userData?.items || [];

  return (
    <div className="flex flex-col items-center p-4">
      <div className="flex gap-2 items-center justify-center mb-4">
        <Button
          onClick={() => console.log(`Userlarning umumiy soni ${user.length}`)}
        >
          Umumiy son: {user.length}
        </Button>
      </div>

      <div className="flex flex-col my-5 w-full">
        <AddUsers onClose={onClose} open={AddOpen} showDrawer={showDrawer} />
        <EditUser />
        <div style={{ overflowX: "auto" }}>
          <Table
            columns={[
              {
                title: "ID",
                dataIndex: "id",
                render: (id, student) => (
                  <span
                    className="cursor-pointer"
                    onClick={() => {
                      setEditOpen(true);
                      setEditStudent(student);
                    }}
                  >
                    {id}
                  </span>
                ),
              },
              {
                title: "Ismi",
                dataIndex: "name",
              },
              {
                title: "Email",
                dataIndex: "email",
                responsive: ["md"],
              },
              {
                title: "Photo",
                dataIndex: "image",
                render: (img: string, user: UserType) =>
                  img &&
                  (img.startsWith("http://") || img.startsWith("https://")) ? (
                    <Image width={40} height={40} src={img} alt={user.name} />
                  ) : (
                    <span>Mavjud emas</span>
                  ),
              },
              {
                title: "Roli",
                dataIndex: "role",
              },

              {
                title: "Saqlangan",
                dataIndex: "createdAt",
                render: (create) => {
                  const date = new Date(create);
                  return <span>{date.toLocaleString()}</span>;
                },
              },
            ]}
            dataSource={user.map((i) => ({ ...i, key: i.id }))}
            pagination={{ pageSize: 5 }}
            scroll={{ x: true }}
          />
        </div>
      </div>
    </div>
  );
}

export default UsersPage;
